  color: #fff;
}
#signUp ion-icon {
  padding-right: 10px;
}
#signUp .terms-text {
  font-size: 14px;
  margin-bottom: 0;
}
#signUp #logo {
  text-align: center;
  margin: 15px auto;
}
#signUp #logo ion-img {
  width: 150px;
  height: 150px;
}
#signUp .spacer {
  margin-top: 20px;
  margin-bottom: 20px;
}
#signUp p {
  margin-bottom: 10px;
}
#signUp .wrap-text {
  white-space: normal;
}
#signUp ion-toggle {
  --track-background: #ddd;
  --track-background-checked: var(--ion-color-primary);
  --handle-background: #b0b0b0;
  --handle-background-checked: #11a987;
  overflow: visible;
}
#signUp ion-input {
  background: #fff;
  border-radius: 6px;
  border: 1px solid #ddd;
  --padding-start: 10px;
}
#signUp ion-button {
  margin-bottom: 20px;
}
#signUp ion-button.facebook {
  --background: #325A9F;
  --background-hover: #4877c9;
  --background-activated: #325A9F;
  --background-focused: #325A9F;
  --color: #ffffff;
}
#signUp ion-button.apple {
  --background: #FFFFFF;
  --background-hover: #BBBBBB;
  --background-activated: #FFFFFF;
  --background-focused: #FFFFFF;
  --color: #000000;
}
#signUp ion-button.google {
  --background: #4285F6;
  --background-hover: #6e9ef1;
  --background-activated: #4285F6;
  --background-focused: #4285F6;
  --color: #ffffff;
